import React from 'react'
import ImageGallery from 'react-image-gallery';
import img1 from "../../Images/phone1.png"
import img2 from "../../Images/phone2.png"
import img3 from "../../Images/phone3.png"
import "react-image-gallery/styles/css/image-gallery.css"
import Right from '../Utilty/Right';
import Left from '../Utilty/Left';

function ProductGaliry() {
    const images = [
        {
          original: `${img1}`,
          thumbnail: `${img1}`
        },
        {
          original: `${img2}`,
          thumbnail: `${img2}`
        },
        {
          original: `${img3}`,
          thumbnail: `${img3}`
        },
     
      ];
  return (
    <>
    <div className='white reduce iiiiii p-4' style={{position:"relative"}}>

        <ImageGallery items={images}  showPlayButton={false}  renderRightNav={Right} renderLeftNav={Left} autoPlay={true}/>
    </div>
    </>
  )
}

export default ProductGaliry