import { Button } from '@mui/material'
import React from 'react'
import { Col, Row } from 'react-bootstrap'

function ProductText() {
  return (
    <>
        <Row>
            <Col className='d-flex flex-column'>
                <span>Electronics</span>
                <span>Pocococooccoocococoococcoo</span>
                <span className='gold'>4.5 <i class="fa-solid fa-star gold"></i></span>
            </Col>
        </Row>
        <Row>
            <Col className='d-flex flex-column'>
                <p>Brand :<span className='fw-bold fs-4 mx-3'>Xiaomi</span></p>
                <div className='d-flex '>
                    <div style={{width:"30px",height:"30px",backgroundColor:"black",borderRadius:"50%" }} className="shadow click mx-1"></div>
                    <div style={{width:"30px",height:"30px",backgroundColor:"gray",borderRadius:"50%" }} className="shadow click mx-1"></div>
                    <div style={{width:"30px",height:"30px",backgroundColor:"blue",borderRadius:"50%" }} className="shadow click mx-1"></div>
                </div>

            </Col>
        </Row>

        <Row>
            <Col className='d-flex flex-column my-2'>
                <span>Specifications :</span>
                <p>Qualcomm® Kryo™ 470 CPU, 8nm process technology Qualcomm® Kryo™ 470 CPU, 8nm process technology Octa-core processor, up to 2.3GHz Adreno™ 618 GPU, up to 800MHz 4th Generation Qualcomm® AI Engine Octa-core processor, up to 2.3GHz Adreno™ 618 GPU, up to 800MHz 4th Generation Qualcomm® AI Engine</p>
            </Col>
        </Row>
        <Row>
            <Col className='d-flex flex-column my-2'>
            <div>
            <span>4500 Le</span>
            <Button variant="outlined" href="#outlined-buttons" className='fit mx-2'>Add To Cart</Button>
            </div>
            </Col>
        </Row>
    </>
  )
}

export default ProductText