import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import UserFavorites from '../../Components/User/UserFavorites'
import UserSidBar from '../../Components/User/UserSidBar'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function UserFavoritesPage() {
  return (
    <>
    <NavBarLogin/>
    <Container>
        <Row className='py-3 '>
            <Col xs='3' sm='3' md='2' className='p-0 white'>
                <UserSidBar/>
            </Col>
            <Col xs='9' sm='9' md='10'>
            <UserFavorites/>
            </Col>
        </Row>
    </Container>
    </>
  )
}

export default UserFavoritesPage