import React from 'react'
import { Col } from 'react-bootstrap'

function CatCard({img}) {
  return (
    <Col xs='6' sm='6' md='4' lg='2' className='my-4 d-flex justify-content-around fit '>
        <div className=' mb-3 click big'>
            <div className=''>
                <img src={img} className='imgCard ' alt='test'/>
            </div>
        </div>
    </Col>
  )
}

export default CatCard