import React from 'react'
import {  Card, Col } from 'react-bootstrap'
import { Link } from 'react-router-dom'

function ProductCard({img ,price ,rate}) {
  return (
    <Col xs='6' sm='6' md='4' lg='3' className='d-flex fit click rot '>
      <Link to={"/products/:id"} style={{textDecoration:"none" ,color:"black"}}>
        <Card style={{ width: '9rem', border:"none" }} >
        

        <Card.Img variant="top" src={img} style={{height:"200px"}} className=""/>
        
        <p><i class="fa-regular fa-heart"></i></p>
        <Card.Body className='p-0'>
            <Card.Text>
            Some quick example text to build on the card title 
            </Card.Text>
            <div className='d-flex justify-content-between'>

            <p >EGP {price}</p>
            <p  className='gold text-end '> {rate} <i class="fa-solid fa-star gold"></i></p>
            </div>
        </Card.Body>
        </Card>
        </Link>
    </Col>
  )
}

export default ProductCard