import React from 'react'
import { Button, Container } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function LoginPage() {
  return (
    <>
      <NavBarLogin/>
    <Container style={{minHeight:"85vh"}} className='d-flex justify-content-center mt-5'>
      <div className='bg-white fit p-4 text-center d-flex flex-column  my-2' style={{height:"350px",width:"300px"}}>
        <p>Login</p>
        <input type={"email"} placeholder="Email" className='m-2'/>
        <input type={"password"} placeholder='Password' className='m-2'/>
        <Button className='m-2' variant='dark'>Login</Button>

      </div>
      <div className=' fit p-4 text-center d-flex flex-column  my-2 backG justify-content-center ' style={{height:"350px",width:"300px"}}>
      <h2 className='text-white'>SignUp</h2>
      <p className='m-2'>SignUp hear if You dont have Account</p>
        <Link to='/regester' style={{textDecoration:"none"}}>

         <Button className='text-white click mx-2' variant='dark'>SignUp</Button>
        </Link>
      </div>
    <Link to={"/admin/allproduct"}>
      <Button>admin</Button>
    </Link>
    <Link to={"/user/allorder"}>
      <Button>user</Button>
    </Link>
    
    </Container>
    </>
  )
}

export default LoginPage