import React from 'react'
import { Button, Container, Row } from 'react-bootstrap'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function PayMeth() {
  return (
    <>
        <NavBarLogin/>
        <Container style={{minHeight:"94vh"}} className='p-3 my-3'>
            <Row>
                <span>Chiose Payment Method</span>
            </Row>
            <Row className='white p-4 mt-3'>
            <div>
            <input type={"radio"} id='pay' name="pay" />
            <label htmlFor='pay'  className='ms-3 mt-3 fw-bold'>By Visa</label>
            </div>
            <div>
            <input type={"radio"} id='Cash' name="pay" />
            <label htmlFor='Cash' className='ms-3 mt-3 fw-bold' >By Cash</label>
            </div>
            </Row>
            <Row className='d-flex justify-content-end m-3 align-items-center'>
            <span className='fit'>4500 Le</span>
            <Button variant='dark' className='fit'>Purchase</Button>

            </Row>
        </Container>
    </>
  )
}

export default PayMeth