import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import UserAddress from '../../Components/User/UserAddress'
import UserSidBar from '../../Components/User/UserSidBar'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function UserAddressPage() {
  return (
    <>
    <NavBarLogin/>
    <Container>
        <Row className='py-3 '>
            <Col xs='3' sm='3' md='2' className='p-0 white'>
                <UserSidBar/>
            </Col>
            <Col xs='9' sm='9' md='10'>
            <UserAddress/>
            </Col>
        </Row>
    </Container>
    </>
  )
}

export default UserAddressPage