import React from 'react'
import { Button, Row } from 'react-bootstrap'
import Multiselect from 'multiselect-react-dropdown';

function AdminAddProduct() {
   const state = {
        options: [{name: 'Sub 2', id: 1},{name: 'Sub 1 ', id: 2}],
        
    };
    const onSelect=()=>{}
    const onRemove=()=>{}
  return (
    <Row className='m-3 flex-column'>
        <div className=' fw-bold fs-4 my-3 '>Add New Product</div>
        <input placeholder='Product Name' type={"text"} className='w-50 reduce m-2 ' required />
        <textarea placeholder='Product Descrption' className=' reduce m-2 w-50 p-2' style={{height:"140px"}} />
        <input placeholder='Product Price' type={"text"} className='w-50 reduce m-2 ' required />
        <input placeholder='Dicount' type={"text"} className='w-50 reduce m-2 '  />
        <select className='m-2 reduce w-50'>
            <option>cat1</option>
            <option>cat2</option>
            <option>cat3</option>
        </select>
        <Multiselect
            className='w-50'
            options={state.options} 
            selectedValues={state.selectedValue} // Preselected value to persist in dropdown
            onSelect={onSelect} // Function will trigger on select event
            onRemove={onRemove} // Function will trigger on remove event
            displayValue="name" // Property name to display in the dropdown options
            placeholder='Sub Category'
            />
        <select className='m-2 reduce w-50'>
            <option>brand1</option>
            <option>brand1</option>
            <option>brand1</option>
        </select>
        <div>
            <span>Colors</span>
            <div className='d-flex click'>

            <div style={{width:"20px",height:"20px",backgroundColor:"black",borderRadius:"50%"}} className='mx-1'></div>
            <div style={{width:"20px",height:"20px",backgroundColor:"red",borderRadius:"50%"}} className='mx-1'></div>
            <div style={{width:"20px",height:"20px",backgroundColor:"blue",borderRadius:"50%"}} className='mx-1'></div>
            <div style={{width:"20px",height:"20px",borderRadius:"50%"}} className='mx-1'> <i class="fa-solid fa-circle-plus"></i></div>
                                    </div>
        </div>
        
        <Button variant='dark' className='fit m-2 my-3'>Save</Button>



    </Row>
  )
}

export default AdminAddProduct