import React from 'react'
import { Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'

function UserAdreesCard() {
  return (
    <Row className='white m-2 reduces'>
        <Row className='justify-content-between my-2  '>
            <span className='fit'>Home</span>
            <div className='d-flex fit'>
            <Link to={"/user/edit-address"} style={{textDecoration:"none",color:"black"}}>

                <div className='mx-1 click'>
                    <span className='mx-1'>Edit</span>
                    <i class="fa-solid fa-pen-to-square"></i>
                </div>
            </Link>
                <div className='mx-1 click'>
                    <span className='mx-1'>Delete</span>
                    <i class="fa-solid fa-trash"></i>
                </div>
            </div>
        </Row>
        <Row className='my-2'>
            <span>213 cairo egypt</span>
        </Row>
        <Row className='my-2'>
            <p>Phone : <span className='fw-light'>01017715716</span></p>
        </Row>
    </Row>
  )
}

export default UserAdreesCard