import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import CatHeader from '../../Components/Catigory/CatHeader'
import ProductContainer from '../../Components/Product/ProductContainer'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'
import Pagi from '../../Components/Utilty/Pagi'
import SearchCount from '../../Components/Utilty/SearchCount'
import SideSearch from '../../Components/Utilty/SideSearch'

function ProductPage() {
  return (
    <>
    <div style={{minHeight:"94vh"}}>

      
      <NavBarLogin/>
      <CatHeader/>
      <Container>
        
      <SearchCount/>
      <Row>
        <Col sm="2">  <SideSearch/></Col>
        <Col sm="10" className='d-flex flex-row justify-content-evenly white p-3 mb-2 '> 
          <ProductContainer />
        </Col>
      </Row>
      
      <Pagi/>
      </Container>
    </div>

    </>
  )
}

export default ProductPage