import React from 'react'

function SideSearch() {
  return (
    <>
        <div className='d-flex flex-column justify-content-center  fit'>

        <h4 className=' mb-3'>Category</h4>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>All</span>
        <input type={'checkbox'}/>
        </div>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>Men</span>
        <input type={'checkbox'}/>
        </div>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>Mobile</span>
        <input type={'checkbox'}/>
        </div>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>Women</span>
        <input type={'checkbox'}/>
        </div>
        <h4 className=' my-2'>Brand</h4>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>All</span>
        <input type={'checkbox'}/>
        </div>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>Samsung</span>
        <input type={'checkbox'}/>
        </div>
        <h4 className=' my-2'>Price</h4>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>From</span>
        <input type={'number'} style={{width:"70px",height:"20px"}}/>
        </div>
        <div className='d-flex justify-content-between'>
        <span className='me-3'>To</span>
        <input type={'number'} style={{width:"70px",height:"20px"}}/>
        </div>

        </div>
    </>
  )
}

export default SideSearch