import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import CartCheak from '../../Components/cart/CartCheak'
import CartItem from '../../Components/cart/CartItem'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function CartPage() {
  return (
    <>
        <NavBarLogin/>
        <Container>
            <Row>
            <div className='mt-4 fs-3 fw-bold'>Shoping Cart</div>
            </Row>
            <Row className='justify-content-center'>
                <Col xs="12" md="9">
                <CartItem/>
                <CartItem/>
                <CartItem/>
                <CartItem/>

                </Col>
                <Col xs="6" md="3">
                <CartCheak/>

                </Col>
            </Row>
        </Container>
    </>
  )
}

export default CartPage