import React from 'react'
import { Row } from 'react-bootstrap'
import UserProduct from './UserProduct'

function UserAllOrder() {
  return (
    <>
        
        <Row className='white m-2'>
            <span className='mb-2'>#44520</span>
                <UserProduct/>
                <UserProduct/>
                <UserProduct/>
            <div className='d-flex justify-content-between'>
                <p className='fw-bold'>Status : <span className='fw-light'>In Progress</span></p>
                <span>4500 Egp</span>
            </div>
        </Row>
    </>
  )
}

export default UserAllOrder