import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import AddBrand from '../../Components/Admin/AddBrand'
import AdminSidBar from '../../Components/Admin/AdminSidBar'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function AdminAddBrandPage() {
  return (
    <>
     <NavBarLogin/>
       <Container style={{}}>
        <Row className='py-3 '>
            <Col xs='3' sm='3' md='2' className='p-0 white'>
                <AdminSidBar/>
            </Col>
            <Col xs='9' sm='9' md='10'>
                <AddBrand/>
            </Col>
        </Row>
    </Container>
    </>
  )
}

export default AdminAddBrandPage