import React from 'react'
import { Button, Container } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function RegesterPage() {
  return (
    <>
      <NavBarLogin/>
    <Container style={{minHeight:"85vh"}} className='d-flex justify-content-center mt-5 '>
    <div className='shadows d-flex' style={{height:"fit-content"}}> 

      <div className=' fit p-4 text-center d-flex flex-column  mt-2 backG justify-content-center ' style={{height:"350px",width:"300px"}}>
      <h2 className='text-white'>Login</h2>
      <p className='m-2'>Wow You have Account</p>
        <Link to='/login' style={{textDecoration:"none"}}>

         <Button className='text-white click mx-2' variant='dark'>Login</Button>
        </Link>
      </div>
      <div className='bg-white fit p-4 text-center d-flex flex-column  mt-2' style={{height:"350px", width:"300px"}}>
        <p>Regester</p>
        <input type={"email"} placeholder="Email" className='m-2'/>
        <input type={"text"} placeholder="username" className='m-2'/>
        <input type={"password"} placeholder='Password' className='m-2'/>
        <Button className='m-2' variant='dark'>Regester</Button>

      </div>

    
    </div>
    </Container>
    </>
  )
}

export default RegesterPage