import React from 'react'
import { Col, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import img1 from "../../Images/phone1.png"


function AdminCheakCard() {
  return (
    <Link to={"/admin/orders/23"} style={{textDecoration:"none",color:"black"}}>

    <Row className='white m-3 reduce p-2 '>
    <Col xs='5' md="5" lg="3" className='m-0 p-0'>
    <img src={img1} alt="2" width={"150px"} />
        
    </Col>

    <Col xs="5" md="5" lg="7">
    <Row >
        <span className='my-2'>#32252</span>
    </Row>
    <Row>
        <p>Smart phone poco x3Smart phone poco x3Smart phone poco x3</p>
    </Row>
    <Row>
        <p>Brand : <span className='fw-bold'>Xiaomi</span></p>
    </Row>
    <Row>
        <div style={{width:"30px", height:"30px", backgroundColor:"black",borderRadius:"50%" ,margin:"0 0 10px 10px" }}></div>
    </Row>
    <Row>
        <span className='fit my-1'>Quantity</span>
        <input type={'number'} style={{width:"70px"}} value='1'/>
    </Row>

    </Col>
    <Col xs='2' className='d-flex flex-column text-end justify-content-between '>
    <i class="fa-solid fa-trash click"></i>
    <span> 4500 Le</span>


    </Col>
</Row>
    </Link>
  )
}

export default AdminCheakCard