import React from 'react'
import { Button, Row } from 'react-bootstrap'

function AdminAddCategory() {
  return (
    <Row className='m-3 flex-column'>

    <div className=' fw-bold fs-4 my-3'>Add New Category</div>
    <span>Category Image</span>
    <i class="fa-solid fa-image fa-4x click m-2"></i>
    <input placeholder='Category Name' type={"text"} className='fit reduce ' />
    <Button variant='dark' className='fit m-3'>Save</Button>
</Row>
  )
}

export default AdminAddCategory