import React from 'react'
import { Col, Row } from 'react-bootstrap'
import {Link} from 'react-router-dom'

function AdminSidBar() {
  return (
    <>
      <Row className=' my-3 p-0 text-center mx-0' style={{minHeight:"77vh",}}>
        <Col className='d-flex flex-column  '>
          <Link to={"/admin/allorder"} style={{textDecoration:"none", color:"black" ,Width:"50%"}} className=' my-3 bord hov '>
          Manage Orders 
          </Link>
          <Link to={"/admin/allproduct"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov '>
              Manage All Product
          </Link>
          <Link to={"/admin/addbrand"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov'>
              Add Brand
          </Link>
          <Link to={"/admin/addcategory"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov'>
              Add Category
          </Link>
          <Link to={"/admin/addsubcategory"} style={{textDecoration:"none", color:"black"}} className='my-3  bord hov'>
              Add Subcategory
          </Link>
          <Link to={"/admin/addproduct"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov'>
              Add Product
          </Link>
          
         
        </Col>
      </Row>
    </>
  )
}

export default AdminSidBar