import React from 'react'
import { Container } from 'react-bootstrap'
import CatHeader from '../../Components/Catigory/CatHeader'
import ProductContainer from '../../Components/Product/ProductContainer'
import ProductDitails from '../../Components/Product/ProductDitails'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'
import Ratings from '../../Components/Utilty/Ratings'

function ProductDetails() {
  return (
    <>
    <div style={{minHeight:"94vh"}}>

        <NavBarLogin/>
        <CatHeader/>
        <Container className=' p-2 '>
            <ProductDitails/>
            <Ratings/>
        </Container>
            <ProductContainer btnTitle={"More"} title={"Recommended for you "} path={"/products"}/>

    </div>
    </>
  )
}

export default ProductDetails