import React from 'react'
import { Button, Row } from 'react-bootstrap'

function AdminAddSubCategory() {
  return (
    <Row className='m-3 flex-column'>
        <div className=' fw-bold fs-4 my-3'>Add New SubCategory</div>
        <input placeholder='SubCategory Name' type={"text"} className='fit reduce m-2 ' />
        <select className='m-2 reduce fit'>
            <option>cat1</option>
            <option>cat2</option>
            <option>cat3</option>
        </select>
        <Button variant='dark' className='fit m-2 my-3'>Save</Button>



    </Row>
  )
}

export default AdminAddSubCategory