import React from 'react'
import Slider from '../../Components/Home/Slider'
import HomeCat from '../../Components/Home/HomeCat'
import ProductContainer from '../../Components/Product/ProductContainer'
import land1 from '../../Images/land.png'
import land2 from '../../Images/land2.png'
import land3 from '../../Images/land3.png'
import disc from '../../Images/disc1.png'
import BrandContainer from '../../Components/Brands/BrandContainer'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function HomePage() {
  return (
    <>
        <NavBarLogin/>

    <Slider img1={land1} img2={land2} img3={land3}/>
    <HomeCat/>
    <ProductContainer title={"Top deals"} path={"/products"} btnTitle={"More"}/>
    <Slider img1={disc} img2={disc} img3={disc}/>
    <ProductContainer title={"Mobile deals"} path={"/products"} btnTitle={"More"}/>
    <BrandContainer/>
    </>
  )
}

export default HomePage