import React from 'react'
import { Container, Row } from 'react-bootstrap'
import ProductCard from './ProductCard'
import SubTitle from '../Utilty/SubTitle'
import img1 from '../../Images/pro1.png'
import img2 from '../../Images/pro2.png'
import img3 from '../../Images/pro3.png'
import img4 from '../../Images/pro4.png'

function ProductContainer({title,path, btnTitle}) {
  return (
    <Container className='bg-white ' style={{overflow:"hidden"}}>

      <SubTitle title={title} btnTitle={btnTitle} path={path}/>
      <Row className=' justify-content-between  my-2 '>

      <ProductCard img={img1} price={19999.00} rate={4.9}/>
      <ProductCard img={img2} price={4000.00} rate={4.5}/>
      <ProductCard img={img3} price={1250.00} rate={3.1}/>
      <ProductCard img={img4} price={12000.00} rate={4.2}/>
      <ProductCard img={img4} price={500.00} rate={2.3}/>

 
      </Row>
    </Container>
  )
}

export default ProductContainer