import React from 'react'
import { Button, Row } from 'react-bootstrap'

function UserProfile() {
  return (
     <>
            <Row><div className=' fw-bold fs-4 my-3'>Profile</div> </Row>
            <Row className='white p-1 m-2 reduces'>
              <div className='d-flex justify-content-between'>
                <p>Name : <span className='fw-light'>Mohamed</span></p>
                <div className='mx-1 click'>
                    <span className='mx-1'>Edit</span>
                    <i class="fa-solid fa-pen-to-square"></i>
                </div>
              </div>
              <p>Phone : <span className='fw-light'>01017715716</span></p>
              <p>Email : <span className='fw-light'>Mohamed@gmail.com</span></p>
            </Row>
            <Row className='m-2'>
            <div className=' fw-bold fs-4 my-3'>Change Password</div>
            <input placeholder='Enter Old Password' type={"password"} className='w-50 p-2 reduce m-2 ' />
            <input placeholder='Enter New Password' type={"password"} className='w-50 p-2 reduce m-2 ' />
            <Row className='justify-content-start m-2'>

        <Button variant='dark' className='fit'>Save </Button>
        </Row>
            </Row>

     </>
  )
}

export default UserProfile