import React from 'react'
import { Card, Col } from 'react-bootstrap'

function BrandCard({img}) {
  return (
    <Col xs='6' sm='6' md='4' lg='2'>
        <Card style={{ width: 'fit-content', border:"none"  }}>
      <Card.Img variant="top" src={img}/>

    </Card>
    </Col>
  )
}

export default BrandCard