function AdminAllProductPage() {
  return (
    <>

    <NavBarLogin/>
    <Container style={{}}>
        <Row className='py-3 '>
            <Col xs='3' sm='3' md='2' className='p-0 white'>
                <AdminSidBar/>
            </Col>
            <Col xs='9' sm='9' md='10'>
                <AdminAllProduct/>
            </Col>
        </Row>
    </Container>
    </>
  )
}

export default AdminAllProductPage