import React from 'react'
import { Button, Col, Container, Row } from 'react-bootstrap'
import UserSidBar from '../../Components/User/UserSidBar'
import NavBarLogin from '../../Components/Utilty/NavBarLogin'

function UserAddAddressPage() {
  return (
    <>
    <NavBarLogin/>
    <Container>
        <Row className='py-3 '>
            <Col xs='3' sm='3' md='2' className='p-0 white'>
                <UserSidBar/>
            </Col>
            <Col xs='9' sm='9' md='10'>
            <Row> <div className=' fw-bold fs-4 my-3'>Add New Address</div>   </Row>
        <input placeholder='Address Name' type={"text"} className='w-50 p-2 reduce m-2 ' />
        <textarea placeholder='Address Descrption' className=' reduce m-2 w-50 p-2' style={{height:"100px"}} />
        <input placeholder='Phone Number' type={"text"} className='w-50 p-2 reduce m-2 ' />
        <Row className='justify-content-start m-2'>

        <Button variant='dark' className='fit'>Add Address</Button>
        </Row>


            </Col>
        </Row>
    </Container>
    </>
  )
}

export default UserAddAddressPage