import React from 'react'
import { Container, Row } from 'react-bootstrap'
import CatCard from '../Catigory/CatCard'
import SubTitle from '../Utilty/SubTitle'
import img1 from '../../Images/cat1.png'
import img2 from '../../Images/cat2.png'
import img3 from '../../Images/cat3.png'
import img4 from '../../Images/cat4.png'
import img5 from '../../Images/cat5.png'


function HomeCat() {
  return (
    <>
    
    <Container className='bg-white'>

      <SubTitle title={"Category"} btnTitle={"More"} path={"/allCategory"}/>
      <Row className='mx-2 justify-content-between  my-2'>

      <CatCard img={img1}/>
      <CatCard img={img2}/>
      <CatCard img={img5}/>
      <CatCard img={img1}/>
      <CatCard img={img3}/>
      <CatCard img={img4}/>
      <CatCard img={img5}/>
      <CatCard img={img2}/>
      <CatCard img={img5}/>
      <CatCard img={img3}/>
      <CatCard img={img4}/>
 
      </Row>
    </Container>
    </>
  )
}

export default HomeCat