import React from 'react'
import { Container, Row } from 'react-bootstrap'

import SubTitle from '../Utilty/SubTitle'
import BrandCard from './BrandCard'
import b1 from '../../Images/brand1.png'
import b2 from '../../Images/brand2.png'
import b3 from '../../Images/brand3.png'
import b4 from '../../Images/brand4.png'
import b5 from '../../Images/brand5.png'

function BrandContainer() {
  return (
    <>


    <Container className='bg-white'>
        <SubTitle title={"Brands"} btnTitle={"More"} path="/allBrands"/>
        <Row className=' justify-content-between  m-3 p-2 '>
            <BrandCard img={b1}/>
            <BrandCard img={b2}/>
            <BrandCard img={b3}/>
            <BrandCard img={b4}/>
            <BrandCard img={b5}/>
            <BrandCard img={b1}/>

        </Row>
    </Container>
    </>
  )
}

export default BrandContainer