import React from 'react'
import { Col, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'

function UserSidBar() {
  return (
    <>
    <Row className=' my-3 p-0 text-center mx-0' style={{minHeight:"77vh",}}>
      <Col className='d-flex flex-column  '>
        <Link to={"/user/allorder"} style={{textDecoration:"none", color:"black" ,Width:"50%"}} className=' my-3 bord hov '>
        Manage Orders 
        </Link>
        <Link to={"/user/favorites"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov '>
            My Favorites
        </Link>
        <Link to={"/user/address"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov'>
            My Address
        </Link>
        <Link to={"/user/profile"} style={{textDecoration:"none", color:"black"}} className='my-3 bord hov'>
            My Profile
        </Link>
        
        
       
      </Col>
    </Row>
  </>
  )
}

export default UserSidBar