import React from 'react'
import { Button, Col, Row } from 'react-bootstrap'
import Stars from './Stars'

function Rate() {
  return (
    <>
    <div className='m-2'>

        <Row>
            <Col className='d-flex my-5'>
                <span className='fw-bold '>Ratings</span>
                <span className='gold mx-2'>4.5 <i className="fa-solid fa-star gold"></i></span>
                <span>(160 Rate)</span>

            </Col>
        </Row>
        <Row>
            <Col className='d-flex'>
                <span className='mx-3 m-1'>Mohamed</span>
                <Stars />
            </Col>
        </Row>
        <Row>
            <Col className='d-flex'>
                <textarea rows='5' cols='180' placeholder='Leave a Comment' className='m-2'/>
            </Col>
        </Row>
        <Row>
            <Col className='d-flex'>
            <Button variant="dark">Comeent</Button>

            </Col>
        </Row>
    </div>
    </>
  )
}

export default Rate