import React from 'react'
import { Button, Col, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'

function CartCheak() {
  return (
    <Row className='text-center white p-2 reduce my-3 d-flex justify-content-center'>
    <Col>
        <div className='my-2'>
            <input placeholder='Enter Coupon' className='in'/>
            <Button variant='dark'>Apply</Button>
        </div>
        <div >4500 LE</div>
        <Link to={"/order/paymethod"} >
            <Button variant='dark' className='my-2'>Cheakout</Button>
        </Link>
    </Col>

    </Row>
  )
}

export default CartCheak