import React from 'react'
import { Row } from 'react-bootstrap'
import Pagi from './Pagi'
import Rate from './Rate'
import RateUser from './RateUser'

function Ratings() {
  return (
    <Row className='white my-2 shadow'>
        <Rate/>
        <RateUser/>
        <RateUser/>
        <RateUser/>
        <RateUser/>
        <Pagi/>
    </Row>
  )
}

export default Ratings