import React from 'react'
import { Button, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import UserAdreesCard from './UserAdreesCard'

function UserAddress() {
  return (
    <>
      <Row><div className=' fw-bold fs-4 my-3'>My Address</div> </Row>
      <Row>
        <UserAdreesCard/>
        <UserAdreesCard/>
        <UserAdreesCard/>
        <Link to={"/user/add-address"} className='m-auto fit' style={{textDecoration:"none",color:"black"}}>
        <Button variant='dark' >Add New Addres</Button>
        </Link>
      </Row>
    </>
  )
}

export default UserAddress