import React from 'react'
import { Container, Form, Navbar}from 'react-bootstrap'
import { Link } from 'react-router-dom'


function NavBarLogin() {
  return (
    <Navbar className='backG' expand="lg">
      <Container >
      <Link to={'/'} style={{textDecoration:"none"}}>

        <Navbar.Brand  className='fw-bolder fs-4'>Fesha</Navbar.Brand>
      </Link>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll" className='justify-content-evenly'>
          <Form className="d-flex w-75 mx-4 ">
            <Form.Control
              type="text"
              placeholder="Search "
              className="me-2 backGrounds"
              aria-label="Search"
              
            />
          </Form>
          <Link to={"/login"} style={{textDecoration:"none" , color:"black"}}>
            <p style={{display:"inline-block"}} className='m-2'>Login</p>
            <i class="fa-solid fa-user"></i>
          
          </Link>
          <Link to={"/cart"} style={{textDecoration:"none" , color:"black"}}>
            <p style={{display:"inline-block"}} className='m-2'>Cart</p>
            <i class="fa-solid fa-cart-shopping "></i>
          </Link>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}

export default NavBarLogin