import React from 'react'
import {   Col } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import img from '../../Images/pro1.png'
function AdminProductCard({price ,rate}) {
return (
    <Col xs='12' sm='6' md='5' lg='4' className='d-flex   mb-3 '>
        <div style={{  border:"none" }} className=" white d-flex flex-column p-2 justify-content-center text-center" >
            <div className='d-flex justify-content-between p-1 m-2 '>
                <span>Edit</span>
                <span>Delete</span>

            </div>
            <Link to={"/products/:id"} style={{textDecoration:"none" ,color:"black"}} className='fit'>
                <img src={img} alt='1' style={{height:"200px",width:"150px"}} className=" m-auto"/>
                <div className=''>

                    <p>
                    Some quick example text to build on the card title 
                    </p>
                    <div className='d-flex justify-content-between'>

                        <p >EGP 5000</p>
                        <p  className='gold text-end '> 5 <i class="fa-solid fa-star gold"></i></p>
                    </div>
                </div>
            
            </Link>
        </div>
    </Col>
    )
}

export default AdminProductCard